<template>
    <div class="commentsDiv">
        <div class="commentsNavDiv">
            <ul class="commentsNavUl">
                <li class="commentsNavLi">
                    <a class="commentsNavA">
                        最新评论
                    </a>
                </li>
            </ul>
        </div>
        <div class="currentComments" v-if="comments">
            <CommentsItem v-for="item in comments.results"
                          :key="item.id"
                          :data="item"
                          :userStatus="{}"
            ></CommentsItem>
        </div>
        <TabPage
        :next="() => tabPage(next)"
        :prev="() => tabPage(prev)"
        ></TabPage>
    </div>
</template>
<script type="text/javascript">
import CommentApi from '@/api/comment.js'

import CommentsItem from './commentItem.vue'
import TabPage from '@/components/common/tabPageWithoutCount.vue'

import utils from '@/utils/base.js'

export default {
    props: {
        _name: {
            type: String,
            require: true
        }
    },
    components: {
        CommentsItem,
        TabPage
    },
    data() {
        return {
            comments: null
        }
    },
    computed: {
        prev: function () {
            if (!this.comments) {
                return ''
            }

            let p = this.comments.previous
            if (p) {
                p = utils.translateQuery(p.split('?')[1])
                p = decodeURIComponent(p.cursor)
                return p
            }
            return ''
        },
        next: function () {
            if (!this.comments) {
                return ''
            }

            let n = this.comments.next
            if (n) {
                n = utils.translateQuery(n.split('?')[1])
                n = decodeURIComponent(n.cursor)
                return n
            }
            return ''
        }
    },
    methods: {
        tabPage: async function (cursor) {
            let res = await CommentApi.getComments({
                'topic_id': this.$route.params.id,
                'topic_type': this._name,
                'page_size': 15,
                'cursor': cursor
            })
            this.comments = res.data
        }
    },

    created: async function () {
        let res = await CommentApi.getComments({
            'topic_id': this.$route.params.id,
            'page_size': 15,
            'topic_type': this._name
        })

        this.comments = res.data
    }
}
</script>
<style type="text/css" scoped>
.commentsNavUl {
    margin-bottom: 10px;
    border: none;
    border-bottom: 1px solid #eee;
    display: flex;
}

.commentsNavLi {

}

.commentsNavA {
    display: block;
    padding: 0 0 4px;
    margin: 0 48px 0 0;
    font-size: 16px;
    color: #4c5157;
    background: transparent;
    border: none;
    border-bottom: 1px solid transparent;
}
</style>