<template>
    <div class="qaDiv">
        <div class="questionDiv" v-if="qaInformation">
           <QaItem v-for="(qa, index) in qaInformation.results"
                   :key="index"
                   :data="qa"
            >
           </QaItem>
        </div>
        <tabPage
        :next="() => { tabPage(currentPage+1) }"
        :prev="() => { tabPage(currentPage-1) }"
        >
        </tabPage>
    </div>
</template>

<script type="text/javascript">
import QuestionApi from '@/api/questions.js'

import QaItem from './courseQaItem.vue'
import tabPage from '@/components/common/tabPageWithoutCount.vue'

export default {
    components: {
        QaItem,
        tabPage
    },
    data() {
        return {
            currentPage: 1,
            qaInformation: null
        }
    },
    methods: {
        tabPage: async function (page) {
            if (page<1) {
                return
            }

            if (!this.qaInformation.next && page > this.currentPage) {
                return
            }

            let res = await QuestionApi.getQuestionList({
                'courseId': this.$route.params.id,
                'page': page
            })
            this.qaInformation = res.data
            this.currentPage = page
        }
    },

    mounted: async function () {
        let res = await QuestionApi.getQuestionList({
            'courseId': this.$route.params.id,
            'page': 1
        })
        this.qaInformation = res.data

    }
}

</script>

<style type="text/css" scoped>
.submitQa {
    display: inline-block;
    margin-top: 12px;
    color: #fff;
    background-color: #08bf91;
    border-color: #08bf91;
    padding: 6px 20px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid transparent;
}

.submitQa:hover {
    color: #fff;
    background-color: #21d6a8;
}

.splitHr {
    margin: 20px 0;
    border: 0;
    border-top: 1px solid #eee;
}

</style>