<template>
    <div class="course">
        <div class="courseTitle" v-if="courseInformation">
            <router-link tag="a" to="/courses"
            class="navTitleA"
            >
                全部课程
            </router-link>
            <span class="separatorSpan">
            </span>
            <div class="courseTagsDiv"
                v-for="(cTag, index) in courseTags"
                :key="cTag"
            >
                <router-link tag="a"
                class="navTitleA"
                :to="{ name: 'courses', query: { tag: cTag } }">
                    {{ cTag }}
                </router-link>
                <span v-if="index!=courseTags.length - 1">,  &nbsp;&nbsp;</span>
            </div>
            <span class="separatorSpan">
            </span>
            <router-link tag="a"
            class="navTitleA"
            :to="{ name: 'course', params: { id: this.$route.params.id } }"
            >
                 {{ courseInformation.name }}
            </router-link>
        </div>
         <div class="courseMain">
            <div class="courseContent">
                <div class="courseInfo" v-if="courseInformation">
                    <div class="courseTitleTypeDiv">
                        <h4>
                            <span class="courseTitleSpan">
                                {{ courseInformation.name }}
                            </span>
                            <span class="courseTypeSpan"
                            :class="courseTypeClass[courseInformation.fee_type]">
                                {{ courseNameType[courseInformation.fee_type] }}
                            </span>
                        </h4>
                    </div>
                    <div class="courseNumbersDiv">
                        <p class="courseNumbersP">{{ courseInformation.students_count}} 人学过</p>
                        <span class="courseNumbersLine">
                        </span>
                        <p class="courseNumbersP"> {{ courseInformation.followers_count}} 人关注</p>
                        <span class="courseNumbersLine">
                        </span>
                        <p class="courseNumbersP">
                            作者：{{ courseInformation.teacher.name }}
                        </p>
                    </div>

                   <div class="courseOperationDiv">
                        <p class="courseDescription">
                            {{ courseInformation.description }}
                        </p>
                    </div>
                </div>
            </div>
            <div class="sider">

            </div>
        </div>
        <div class="courseKnowledgePointsDiv">
            <ul class="courseNavUl">
                <li class="courseNavLi">
                    你将会学到
                </li>
            </ul>
            <div class="knowledgePointsDiv">
                <div class="pointsDiv"
                    v-for="points in courseKnowledgePoints"
                    :key="points"
                >
                    {{ points }}
                </div>
            </div>
        </div>
        <div class="courseContents">
                    <div class="courseNavDiv">
                        <ul class="courseNavUl">
                            <li class="courseNavLi">
                                课程内容
                            </li>
                        </ul>
                    </div>
                    <div class="labContents" v-if="courseLabs">
                        <div class="labItem"
                             v-for="(courseProcess, index) in courseContent.labs"
                             :key="index">
                            <div class="labHeader">
                                <div class="labText">
                                    <div class="labIcon labTextItem">
                                        <img v-if="courseProcess.type=='classic'"
                                        src="data:image/png;base64," />
                                        <img v-if="courseProcess.type=='course_challenge'"
                                        src="data:image/png;base64,">
                                    </div>
                                    <div class="labCategory labTextItem">
                                        {{ typeDict[courseProcess.type] }}
                                    </div>

                                    <div class="labName labTextItem">
                                        {{ courseProcess.name }}
                                    </div>
                                </div>
                            </div>
                            <div class="labDescription">
                                <div class="innerLabDescription"
                                     v-if="courseProcess.points !== ''"
                                >
                                    {{ courseProcess.points }}
                                </div>
                            </div>
                        </div>
                    </div>
        </div>
        <div class="courseLongDescriptionDiv">
            <ul class="courseNavUl">
                <li class="courseNavLi">
                    课程介绍
                </li>
            </ul>

            <div class="mainDescriptionDiv">
                <VueMarkDown v-if="courseInformation.long_description" :source="courseInformation.long_description"></VueMarkDown>
            </div>
        </div>
         <div class="courseTeacherDiv">
                    <ul class="courseNavUl">
                        <li class="courseNavLi">
                            课程教师
                        </li>
                    </ul>
                    <div class="courseAuthorMainDiv" v-if="courseInformation">
                        <div class="authorBaseInfo">
                            <div class="authorAvatar">
                                <router-link tag="a" :to="{ name: 'user', params: {id: courseInformation.teacher.id} }">
                                    <img :src="courseInformation.teacher.avatar_url" class="courseTeacherAvatar">
                                </router-link>
                            </div>
                            <div class="authorNameCourseHistory">
                                <span class="authorNameSpan">
                                    {{ courseInformation.teacher.name }}
                                    <span class="authorHistorySpan">共发表过{{ courseInformation.teacher.published_courses_count }}门课程</span>
                                </span>
                                <span class="teacherDescription">
                                    {{ courseInformation.teacher.teacher_info.description }}
                                </span>
                                <router-link tag="a"
                                             :to="{ name: 'user', params: {id: courseInformation.teacher.id} }"
                                             class="lookUpTeacher"
                                >
                                    查看老师的所有课程 >
                                </router-link>
                            </div>
                        </div>
                    </div>
        </div>
        <div class="courseDiscuss" v-if="courseInformation">
                        <div class="discussNavDiv">
                            <ul class="discussNavUl">
                                <li class="discussNavLi">
                                    <a href="javascript:;"
                                       class="discussNavA"
                                       @click="changeDiscussNav('comment')"
                                       :class="nav === 'comment' ? 'activeDiscussNavA' : ''"
                                    >
                                        课程评论 ({{ courseInformation.comments_count }})
                                    </a>
                                </li>
                                <li class="discussNavLi">
                                    <a href="javascript:;"
                                       class="discussNavA"
                                       @click="changeDiscussNav('report')"
                                       :class="nav === 'report' ? 'activeDiscussNavA' : ''"
                                    >
                                        实验报告 ({{ courseInformation.labreports_count }})
                                    </a>
                                </li>
                                <li class="discussNavLi">
                                    <a href="javascript:;"
                                       class="discussNavA"
                                       @click="changeDiscussNav('qa')"
                                       :class="nav === 'qa' ? 'activeDiscussNavA' : ''"
                                    >
                                        实验问答 ({{ courseInformation.questions_count }})
                                    </a>
                                </li>
                            </ul>
                        </div>
                        <div class="discussMainDiv">
                            <keep-alive>
                                <component :is="discussComponentDict[nav]" :_name="'course'" :labInfo="courseContent"></component>
                            </keep-alive>
                        </div>
                    </div>
        
    </div>
</template>

<script type="text/javascript">
import CourseApi from '@/api/course.js'
import VueMarkDown from 'vue-markdown'
import Comment from '@/components/common/comment.vue'
import Report from './courseReport.vue'
import Qa from './courseQa.vue'

export default {
    components: {
        VueMarkDown,
        Comment,
        Report,
        Qa,
    },
    data() {
        return {
            courseInformation: null,
            courseTypeClass: {
                'free': 'free_course_p',
                'member': 'vip_course_p',
                'bootcamp': 'training_course_p',
                '': ''
            },
            courseNameType: {
                'free': '免费',
                'member': '会员',
                'bootcamp': '训练营'
            },
            courseLabs: null,
            typeDict: {
                'classic': '实验',
                'course_challenge': '挑战'
            },
            nav: 'comment',
            discussComponentDict: {
                'comment': 'Comment',
                'report': 'Report',
                'qa': 'Qa',
            }
        }
    },
    methods: {
        changeDiscussNav(nav) {
            this.nav = nav
        }
    },
    computed: {
        courseTags: function () {
            return this.courseInformation.tags
        },
        courseKnowledgePoints: function () {
            if (!this.courseInformation) {
                return []
            }
            return this.courseInformation.points
        },
        courseContent: function () {
            if (!this.courseLabs) {
                return {
                    labs: []
                }
            }

            return this.courseLabs[0]
        }
    },
    async created () {
        // 用 this.$route.params.id 来获取我们设置在路由路径中的 :id
        let res = await CourseApi.getCourseInformation(this.$route.params.id)

        this.courseInformation = res.data
        debugger

        let resTwo = await CourseApi.getCourseLabs(this.$route.params.id)
        this.courseLabs = resTwo.data
    }
}

</script>

<style type="text/css" scoped>
