<template>
    <div class="expertimentalReport">
        <div class="expertimentalCategory">
            <reportCategoryItem :category="'全部'"
                                :categoryId="0"
                                :currentReport="currentReport"
                                :tabReport="() => { tabReport(0) }"
            >
            </reportCategoryItem>
            <reportCategoryItem v-for="lab in labInfo.labs"
                                :key="lab.id"
                                :category="lab.name"
                                :categoryId="lab.id"
                                :currentReport="currentReport"
                                :tabReport="() => { tabReport(lab.id) }"
            >
            </reportCategoryItem>
        </div>
        <div class="expertimentalReportContent" v-if="reportInfo">
            <reportContentItem class="reportContentItem"
                               v-for="(item, index) in reportInfo.results"
                               :key="index"
                               :data="item"
            >
            </reportContentItem>
        </div>
        <tabPage
        :next="() => { tabPage(next) }"
        :prev="() => { tabPage(prev) }"
        >
        </tabPage>
    </div>
</template>
<script type="text/javascript">
import reportApi from '@/api/reports.js'

import reportCategoryItem from './courseReportCategoryItem.vue'
import reportContentItem from './courseReportContentItem.vue'
import tabPage from '@/components/common/tabPageWithoutCount.vue'

import utils from '@/utils/base.js'

export default {
    props: {
        labInfo: {
            required: true,
            type: Object
        }
    },
    components: {
        reportCategoryItem,
        reportContentItem,
        tabPage
    },
    data() {
        return {
            reportInfo: null,
            currentReport: 0
        }
    },
    computed: {
        prev: function () {
            if (!this.reportInfo) {
                return ''
            }

            let p = this.reportInfo.previous
            if (p) {
                p = utils.translateQuery(p.split('?')[1])
                p = decodeURIComponent(p.cursor)
                return p
            }
            return ''
        },
        next: function () {
            if (!this.reportInfo) {
                return ''
            }
            let n = this.reportInfo.next
            if (n) {
                n = utils.translateQuery(n.split('?')[1])
                n = decodeURIComponent(n.cursor)
                return n
            }
            return ''
        }
    },
    methods: {
        tabPage: async function (cursor) {
            let res = await reportApi.getLabReports({
                'course_id': this.$route.params.id,
                'lab_id': this.currentReport,
                'page_size': 16,
                'cursor': cursor
            })
            this.reportInfo = res.data
        },
        tabReport: async function (labId) {
            this.currentReport = labId
            let res = await reportApi.getLabReports({
                'course_id': this.$route.params.id,
                'lab_id': this.currentReport,
                'page_size': 16
            })
            this.reportInfo = res.data
        }
    },
    mounted: async function () {
        let res = await reportApi.getLabReports({
            'course_id': this.$route.params.id,
            'page_size': 10
        })

        this.reportInfo = res.data
    }
}

</script>
<style type="text/css" scoped>

.expertimentalCategory {
    display: flex;
    flex-wrap: wrap;
}

.expertimentalReportContent {
    display: flex;
    flex-wrap: wrap;
}

.reportContentItem {
    width: 25%;
}
</style>