import http from "./base.js";

export default {
  getComments(commentargs) {
    return http.get(`v2/comments/`, { params: commentargs });
  },
  postComment(commentargs) {
    return http.post(`v2/comments/`, commentargs);
  },
  getCommentsUserstatus(commentargs) {
    return http.get(`v2/comments/userstatus/`, { params: commentargs });
  },
  deleteComment(commentargs) {
    let id = commentargs.id;

    return http.delete(`v2/comments/${id}/`);
  },
  replyComment(commentargs) {
    return http.post(`v2/comments/`, commentargs);
  }
};