import http from "./base.js";

export default {
  getCourseInformation(courseid) {
    return http.get(`v2/courses/${courseid}`);
  },

  getCourseLabs(courseid) {
    return http.get(`v2/courses/${courseid}/labs`);
  },

  // 下面是登录后会有的内容
  getCourseUserstatus(query) {
    return http.get("v2/courses/userstatus/", { params: query });
  },

  follow(query) {
    let id = query.id;
    return http.put(`v2/courses/${id}/follow/`);
  },

  unfollow(query) {
    let id = query.id;
    return http.delete(`v2/courses/${id}/follow/`);
  },

  joinCourse(query) {
    let id = query.id;
    return http.post(`v2/courses/${id}/join/`);
  }
};