import http from "./base.js";

export default {
  getLabReports(args) {
    return http.get(`v2/labreports/`, { params: args });
  },
  getReportInformation(args) {
    return http.get(`v2/labreports/${args.id}/?view=detail`);
  },
  getLearnData(args) {
    return http.get(`v2/labreports/${args.id}/learn-data/`);
  },
  getRelated(args) {
    return http.get(`v2/labreports/${args.id}/related/`);
  }
};