<template>
    <div class="reportCategoryDiv">
        <a href="javascript:;"
           class="reportCategoryA"
           :class="currentReport === categoryId ? 'active' : ''"
           @click="tabReport"
        >
            {{ category }}
        </a>
    </div>
</template>
<script type="text/javascript">
export default {
    props: {
        category: {
            type: String,
            require: true
        },

        categoryId: {
            type: Number,
            require: true
        },
        currentReport: {
            type: Number,
            require: true
        },
        tabReport: {
            type: Function,
            require: true
        }

    }
}

</script>
<style type="text/css" scoped>
.reportCategoryA {
    display: block;
    padding: 8px 24px;
    background: #e6ecf3;
    color: #55667c;
    margin: 4px 2px;
}

.reportCategoryA:hover {
    color: #55667c;
}

.active {
    background-color: #55667c;
    color: #fff;
}

.active:hover {
    color: #fff;
}
</style>